import React, { Component } from 'react';
import ReactLoading from "react-loading";

class List extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             items: [],
             isLoaded: false
        }
    }

    componentDidMount() {

      //http://localhost:/react-api/realest.json
        const urlT = "https://jsonplaceholder.typicode.com/photos"
        fetch(urlT)
        let randNum = (Math.floor(Math.random() * urlT.length)+1);
        fetch(`https://jsonplaceholder.typicode.com/photos?id=${randNum}`)
            .then(res => res.json())
            .then(json => {
                this.setState({
                    isLoaded: true,
                    items: json,
                })
                
               
        
            });
    }
    
    render() {

        var {isLoaded, items } = this.state;

        if (!isLoaded){
            return  <ReactLoading type={"bars"} color={"black"} />
        } else {

        
            return (
<div>

{items.map(item => (
<div key={item.id} className="card promoting-card">


  <div  className="card-body d-flex flex-row">

    
    <img src={item.url} className="rounded-circle mr-3" height="50px" width="50px" alt="avatar" />

    <div>

    
      <h4 className="card-title font-weight-bold mb-2">{item.title}</h4>
      
      <p className="card-text"><i className="far fa-clock pr-2"></i>{item.thumbnailUrl}</p>

    </div>

  </div>

  
  <div className="view overlay">
    <img className="card-img-top rounded-0" src={item.url} alt="Card cap" />
    <a href="#!">
      <div className="mask rgba-white-slight"></div>
    </a>
  </div>


  <div className="card-body">

    <div className="collapse-content">


      <p className="card-text collapse" id="collapseContent">Recently, we added several exotic new dishes to our restaurant menu. They come from countries such as Mexico, Argentina, and Spain. Come to us, have some delicious wine and enjoy our juicy meals from around the world.</p>
    
      <button className="btn btn-flat red-text p-1 my-1 mr-0 mml-1 collapsed" data-toggle="collapse" href="#" aria-expanded="false" aria-controls="collapseContent"></button>
      <i className="fas fa-share-alt text-muted float-right p-1 my-1" data-toggle="tooltip" data-placement="top" title="Share this post"></i>
      <i className="fas fa-heart text-muted float-right p-1 my-1 mr-3" data-toggle="tooltip" data-placement="top" title="I like it"></i>

    </div>

  </div>

</div>


))};








                 
</div>
        );

        }
    }
}

export default List
